'use client';

import { FC, useCallback, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import Image from 'next/image';
import { twMerge } from 'tailwind-merge';
import Button from '@/components/common/Button';

/**
 * ÂõæÁâá‰∏ä‰º†ÁªÑ‰ª∂ÁöÑÂ±ûÊÄßÊé•Âè£
 * @interface ImageUploaderProps
 */
interface ImageUploaderProps {
  /** Ëá™ÂÆö‰πâÁ±ªÂêç */
  className?: string;
  /** ‰∏ä‰º†ÂÆåÊàêÂõûË∞É */
  onUpload?: (file: File) => void;
  /** ÊúÄÂ§ßÊñá‰ª∂Â§ßÂ∞èÔºàMBÔºâ */
  maxSize?: number;
}

/**
 * ÂõæÁâá‰∏ä‰º†ÁªÑ‰ª∂
 * @component ImageUploader
 * @param {ImageUploaderProps} props - ÂõæÁâá‰∏ä‰º†ÁªÑ‰ª∂ÁöÑÂ±ûÊÄß
 */
const ImageUploader: FC<ImageUploaderProps> = ({
  className,
  onUpload,
  maxSize = 5
}) => {
  const [preview, setPreview] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const onDrop = useCallback((acceptedFiles: File[]) => {
    setError(null);
    const file = acceptedFiles[0];

    if (file) {
      // Ê£ÄÊü•Êñá‰ª∂Â§ßÂ∞è
      if (file.size > maxSize * 1024 * 1024) {
        setError(`Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá ${maxSize}MB`);
        return;
      }

      // ÂàõÂª∫È¢ÑËßàURL
      const previewUrl = URL.createObjectURL(file);
      setPreview(previewUrl);
      onUpload?.(file);

      // Ê∏ÖÁêÜÈ¢ÑËßàURL
      return () => URL.revokeObjectURL(previewUrl);
    }
  }, [maxSize, onUpload]);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/*': ['.jpeg', '.jpg', '.png', '.gif']
    },
    maxFiles: 1
  });

  const clearPreview = useCallback(() => {
    setPreview(null);
    setError(null);
  }, []);

  const handleStartConvert = useCallback(() => {
    // TODO: ÂÆûÁé∞ËΩ¨Êç¢ÈÄªËæë
    console.log('ÂºÄÂßãËΩ¨Êç¢');
  }, []);

  return (
    <div className={twMerge('space-y-4', className)}>
      <div
        {...getRootProps()}
        className={twMerge(
          'border-2 border-dashed rounded-lg p-8 text-center cursor-pointer transition-colors',
          isDragActive ? 'border-blue-500 bg-blue-50' : 'border-gray-300 hover:border-gray-400',
          preview ? 'border-green-500' : ''
        )}
      >
        <input {...getInputProps()} />
        
        {preview ? (
          <div className="relative w-full aspect-video">
            <Image
              src={preview}
              alt="È¢ÑËßàÂõæ"
              fill
              className="object-contain rounded-lg"
              unoptimized
            />
          </div>
        ) : (
          <div className="space-y-2">
            <div className="text-4xl text-gray-400">üì∏</div>
            <p className="text-gray-600">
              {isDragActive
                ? 'ÊîæÂºÄ‰ª•‰∏ä‰º†ÂõæÁâá'
                : 'ÊãñÊãΩÂõæÁâáÂà∞Ê≠§Â§ÑÊàñÁÇπÂáª‰∏ä‰º†'}
            </p>
            <p className="text-sm text-gray-500">
              ÊîØÊåÅ JPG„ÄÅPNG„ÄÅGIF Ê†ºÂºèÔºåÊúÄÂ§ß {maxSize}MB
            </p>
          </div>
        )}
      </div>

      {error && (
        <p className="text-sm text-red-500">{error}</p>
      )}

      {preview && (
        <div className="flex justify-end space-x-4">
          <Button
            variant="outline"
            onClick={clearPreview}
          >
            ÈáçÊñ∞‰∏ä‰º†
          </Button>
          <Button onClick={handleStartConvert}>
            ÂºÄÂßãËΩ¨Êç¢
          </Button>
        </div>
      )}
    </div>
  );
};

export default ImageUploader; 